import React, {
	FC,
} from "react";

import { colors } from "../../styleguide";
import Typography from "../../Typography/Typography";

import {
	NavItemsWrapper,
	NavWrapper,
	UserIconButton,
	UserMenuButtonWrapper,
	UserMenuItem,
	UserMenuItemWrapper,
	UserMenuSubtext,
} from "./LocalNavbar.styles";

export interface LocalNavbarProps {
	dataQA: string
	ariaLabel: string
	className?: string
	brandName?: string
	brandUrl?: string
	userName?: string
	userEmail?: string
	shuffleButtonOnClick?: React.MouseEventHandler<HTMLButtonElement>
	logoutButtonOnClick?: React.MouseEventHandler<HTMLButtonElement>
	children?: React.ReactNode
}

/** @deprecated Use LocalNavigation */
const LocalNavbar: FC<LocalNavbarProps> = ({
	dataQA,
	ariaLabel,
	children,
	className,
	brandName,
	brandUrl,
	userName,
	userEmail,
	shuffleButtonOnClick,
	logoutButtonOnClick,
}) => (
	<NavWrapper
		aria-label={ariaLabel}
		data-qa={dataQA}
		className={className}
	>
		<NavItemsWrapper>
			<div>
				{children}
			</div>
			<div>
				<UserMenuItemWrapper>
					<UserMenuItem>
						<Typography
							dataQA="brand-name"
							variant="bodyCopyBold"
							component="p"
							color={colors.white}
							showEllipsis
						>
							{brandName}
						</Typography>
						<UserMenuSubtext
							dataQA="brand-subtext"
							variant="caption"
							component="p"
							color={colors.white}
							m="0"
							showEllipsis
						>
							{brandUrl}
						</UserMenuSubtext>
					</UserMenuItem>
					<UserMenuButtonWrapper>
						<UserIconButton
							icon="Shuffle"
							variant="primary"
							primaryButtonColor="black"
							dataQA="shuffle-button"
							size="Large"
							onClick={shuffleButtonOnClick}
							aria-label="switch client"
						/>
					</UserMenuButtonWrapper>
				</UserMenuItemWrapper>
				<UserMenuItemWrapper>
					<UserMenuItem>
						<Typography
							dataQA="user-name"
							variant="bodyCopyBold"
							component="p"
							color={colors.white}
							showEllipsis
						>
							{userName}
						</Typography>
						<UserMenuSubtext
							dataQA="user-email"
							variant="caption"
							component="p"
							color={colors.white}
							m="0"
							showEllipsis
						>
							{userEmail}
						</UserMenuSubtext>
					</UserMenuItem>
					<UserMenuButtonWrapper>
						<UserIconButton
							icon="LogOut"
							variant="primary"
							primaryButtonColor="black"
							dataQA="logout-button"
							size="Large"
							onClick={logoutButtonOnClick}
							aria-label="Logout"
						/>
					</UserMenuButtonWrapper>
				</UserMenuItemWrapper>
			</div>
		</NavItemsWrapper>
	</NavWrapper>
);

LocalNavbar.displayName = "LocalNavbar";
export default LocalNavbar;
